/*------------------------------------------------------------
	Banner
------------------------------------------------------------*/
.c-banner{
	position: relative;
	&::after{
		position: absolute;
    content: "";
    height: 35vh;
    bottom: -15vh;
    left: 0;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
		background-image: url(../../assets/images/banner-bg.svg);
		@include max(1024px){
			bottom: -18vh;
			height: 30vh;
		}
	}
	&__content{
		position: absolute;
    width: 90%;
    padding-top: 20rem;
    top: 0;
    left: 50%;
    text-align: center;
    transform: translateX(-50%);
    z-index: 10;
    color: #fff;
	}
	&__ttl{
		position: relative;
    margin-right: -2rem;
		font-size: 6rem;
    font-weight: 600;
    text-transform: uppercase;
    color: currentColor;
    white-space: pre-line;
    letter-spacing: 2rem;
    position: relative;
    margin-bottom: 0;
		@include sp{
			font-size: 2.4rem;
			margin-bottom: .5rem;
		}
		&::after{
			position: absolute;
			content: "";
			width: 50%;
			height: 100%;
			-webkit-filter: blur(5rem);
			filter: blur(5rem);
			left: 50%;
			top: 0;
			background-color: hsla(0,0%,100%,.25);
			-webkit-transform: translateX(-50%);
			transform: translateX(-50%)
		}
	}
	&__sub{
		letter-spacing: .1rem;
    white-space: pre-line;
    width: 60%;
    margin: 0 auto 1rem;
	}
	&__item{
		img{
			width: 100%;
			height: 60rem;
			object-fit: cover;
			-webkit-filter: brightness(.85);
			filter: brightness(.85);
		}
	}
}